const nunitoStyle = {
    fontFamily: 'Nunito, sans-serif',
    fontWeight: 700,
};

function slugify(text) {
	const regex = /\(tạm đặt\)/gi;
    const slug = getSlug(text.replace(regex, ''));
    return slug;
}


function LeftPanel({ open }) {
    const [expandedCategories, setExpandedCategories] = useState([]);
    const [selectedPoemIndex, setSelectedPoemIndex] = useState(-1);

    const handleCategoryClick = (index) => {
        if (expandedCategories.includes(index)) {
            setExpandedCategories(expandedCategories.filter((item) => item !== index));
        } else {
            setExpandedCategories([...expandedCategories, index]);
        }
    };

    return (
        <Drawer anchor="left" open={open}>
            <div className="drawer-content">
                <List component="nav">
                    {categories.map((category, index) => (
                        <div key={index}>
                            <ListItem button>
                                <div
                                    className="category-container"
                                    style={{ ...nunitoStyle, paddingLeft: '16px' }}
                                >
                                    <Link
                                        to={category.link}
                                        style={{ textDecoration: 'none', color: 'inherit' }}
                                    >
                                        {category.title}
                                    </Link>
                                </div>
                                <div
                                    className="icon-container"
                                    onClick={() => handleCategoryClick(index)}
                                    style={{ ...nunitoStyle, paddingLeft: '24px' }}
                                >
                                    {expandedCategories.includes(index) ? (
                                        <ExpandLessIcon />
                                    ) : (
                                        <ExpandMoreIcon />
                                    )}
                                </div>
                            </ListItem>
                            {expandedCategories.includes(index) && (
                                <List>
                                    {category.poems.map((poem, poemIndex) => (
                                        <ListItem
                                            key={poemIndex}
                                            className="poem-item"
                                            style={{
                                                ...nunitoStyle,
                                                paddingLeft: '64px',
                                                backgroundColor:
                                                    poemIndex === selectedPoemIndex ? '#f0f0f0' : 'inherit',
                                            }}
                                        >
                                            <Link
                                                to={`/tho${category.link}/${slugify(poem)}`}
                                                style={{ textDecoration: 'none', color: 'inherit' }}
                                                onClick={() => setSelectedPoemIndex(poemIndex)}
                                            >
                                                {poem}
                                            </Link>
                                        </ListItem>
                                    ))}
                                </List>
                            )}
                        </div>
                    ))}
                </List>
            </div>
        </Drawer>
    );
}

export default LeftPanel;